"use client";
import { useEffect, useState } from "react";
import API_URL from "../config";

export default function TasksPage() {
  const [tasks, setTasks] = useState([]);

  useEffect(() => {
    const fetchTasks = async () => {
      const token = localStorage.getItem("token");
      const res = await fetch(`${API_URL}/api/tasks`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      const data = await res.json();
      setTasks(data);
    };
    fetchTasks();
  }, []);

  const markDone = async (id) => {
    const token = localStorage.getItem("token");
    await fetch(`${API_URL}/api/tasks/${id}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ status: "Completed" }),
    });
    setTasks(tasks.map(t => t._id === id ? { ...t, status: "Completed" } : t));
  };

  return (
    <div className="max-w-md mx-auto space-y-4">
      <h1 className="text-2xl font-bold text-center mb-4">Your Tasks</h1>
      {tasks.length === 0 ? (
        <p className="text-center text-gray-600">No tasks yet.</p>
      ) : (
        tasks.map(task => (
          <div
            key={task._id}
            className="bg-white p-4 rounded shadow flex justify-between items-center"
          >
            <div>
              <h2 className="font-semibold">{task.title}</h2>
              <p
                className={`text-sm ${
                  task.status === "Completed" ? "text-green-600" : "text-red-500"
                }`}
              >
                {task.status}
              </p>
            </div>
            {task.status !== "Completed" && (
              <button
                onClick={() => markDone(task._id)}
                className="primary text-sm"
              >
                Mark Done
              </button>
            )}
          </div>
        ))
      )}
    </div>
  );
}
